@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,600;0,700;0,800;1,400;1,900&display=swap');
$color1: rgb(17, 16, 16);
$color2: rgb(214, 214, 214);
$color3: rgba(102, 56, 240, 1);
$color4: rgba(92, 201, 245, 1);


%message-shared {
    color: aliceblue;
    background-color: $color3;
    padding: 9px 14px;
    font-size: 16px;
    font-weight: 600;
    border: none;
    border-radius: 4px;
}

.box {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: $color2;
    width: 604px;
    font-family: Poppins;
    padding: 20px;
    margin: 500px auto;
    order: 1;
    
    h3{
    font-size: 24px;
    font-weight: 600;
    text-align: left;

    }
    
    h4{
    font-size: 16px;
    font-weight: 400;
    text-align: left;
    }
    
    
}


button{
    text-align: left;
    @extend %message-shared;
}

#open:hover{
    color: $color4;
}

.modal-container {
    display: flex;
    align-items: center;
    width: 752px;
    height: 543px;
    margin: auto;
    font-family: Poppins;
    position: fixed;
    top: 0; bottom: 0; left: 200px;
    background-color: aliceblue;
    
    img{
   margin-top: -500px; 
   padding: 20px;
    }
    
    p{
    text-align: left;
    }
}

.button2{
    display: flex;
    
    a{
    margin-left: 20px;
    text-decoration: none;
    color: $color3;
}

.gradient {
    width: 375px;
    background-image: linear-gradient(rgba(102, 56, 240, 1),rgba(92, 201, 245, 1));
    height: 543px;
}

.popup{
    width: 313px;
    padding: 37px;
    
    h3{
    font-size: 24px;
    font-weight: 600;
    }
    
    p{
    font-size: 16px;
    font-weight: 400;
    }
    }
}

